async function countdown() {
  const interval = 1000
  let end =
    localStorage.getItem("endTime") ||
    new Date().getTime() + 24 * 60 * 60 * 1000
  localStorage.setItem("endTime", end)

  function addZero(num) {
    if (num >= 0 && num <= 9) {
      return "0" + num
    } else {
      return num
    }
  }

  while (true) {
    const now = new Date().getTime()
    const distance = end - now

    if (distance < 0) {
      end = new Date().getTime() + 24 * 60 * 60 * 1000
      localStorage.setItem("endTime", end)
    }

    const hours = addZero(
      Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
    )
    const minutes = addZero(
      Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
    )
    const seconds = addZero(Math.floor((distance % (1000 * 60)) / 1000))

    document.querySelectorAll(".timer__block").forEach((timerBlock) => {
      timerBlock.innerHTML = `
            <span class="timer__number timer__number_purple"><span>${String(
              hours
            ).substring(0, 1)}</span> <span>${String(hours).substring(
        1,
        2
      )}</span></span>
            <div class="timer__circle"></div>
            <span class="timer__number timer__number_orange"><span>${String(
              minutes
            ).substring(0, 1)} </span> <span>${String(minutes).substring(
        1,
        2
      )}</span></span> 
            <div class="timer__circle"></div>
            <span class="timer__number timer__number_purple"><span>${String(
              seconds
            ).substring(0, 1)}</span> <span>${String(seconds).substring(
        1,
        2
      )}</span></span>
        `
    })

    await new Promise((resolve) => setTimeout(resolve, interval))
  }
}

countdown()
