const preloader = document.querySelector(".preloader")
const sendForm = async (inputs, id = 1) => {
  inputs[0].value = inputs[0].value
    .split(" ")
    .map((data) => data[0].toUpperCase() + data.slice(1).toLowerCase())
    .join(" ")

  const data = {}
  inputs.forEach((value) => {
    data[value.name] = value.value.trim()
  })

  $.ajax({
    url: "/ajax/default_mf.php",
    type: "POST",
    cache: false,
    data: data,
    dataType: "html",
    beforeSend: () => {
      preloader.classList.add("shown")
      inputs.forEach((input) => {
        input.disabled = true
      })
    },
    success: () => {
      setTimeout(() => {
        preloader.classList.remove("shown")
        window.location.href = "https://myforce.ru/"
        inputs.forEach((input) => {
          input.disabled = false
        })
      }, 3000)
    },
    error: () => {
      preloader.classList.remove("shown")
      swal("Ошибка", "Данные не отправлены", "error")
      inputs.forEach((input) => {
        input.disabled = false
      })
    },
  })
}

export default sendForm
